<template lang="pug">
  div.app
    header
    TagContainer(
      v-show="getSelectedTags.length > 0"
      v-on:clearTags="clearFilter",
      v-on:removeTag="removeTag",
    )
    div(:class="{'tag-open': getSelectedTags.length > 0}").job-list
      div(v-for="opening in openings")
        JobItem(
          :isFeatured="opening.isFeatured"
          :isNew="opening.isNew"
          :opening="opening"
          v-on:addToFilter="addToFilter"
        )
</template>

<script>
import { mapGetters } from 'vuex';

// @ is an alias to /src
import TagContainer from '@/components/TagContainer.vue';
import JobItem from '@/components/JobItem.vue';

export default {
  name: 'Home',
  components: {
    TagContainer,
    JobItem
  },
  computed: {
    ...mapGetters(['getAllOpenings', 'getSelectedTags', 'filteredOpenings'])
  },
  data () {
    return {
      openings: []
    };
  },
  mounted () {
    this.openings = this.getAllOpenings;
  },
  methods: {
    addToFilter (f) {
      this.$store.dispatch("aAddTag", f);

      this.openings = this.filteredOpenings;
    },
    removeTag (t) {
      const arr = this.getSelectedTags;
      
      this.$store.dispatch("aRemoveTag", arr.indexOf(t));

      this.openings = this.filteredOpenings;
    },
    clearFilter () {
      this.$store.dispatch("aClearTags");

      this.openings = this.getAllOpenings;
    }
  }
};
</script>

